@value colors: '../../css/colors.css';
@value whiteBasic, darkGrey from colors;

.footer{
    width: 100%;
    max-height: 62px;
    background-color: darkGrey
}

.wrap{
    margin: 0 auto;
    max-width: 1360px;
    padding: 14px 40px;
    display: flex;
    justify-content: space-between
}

.social{
    width: 105px;
    display: flex;
    justify-content: space-between;
}

.icon{
    width: 36px;
    height: 36px;
}

.twiter{
    background: url("./img/instagram.png") no-repeat center;
    background-size: 100%;
}

.facebook{
    background: url("./img/Facebook.png") no-repeat center;
    background-size: 100%;
}

.contact{
    font-family: RubikRegular;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: whiteBasic
}

.shoful{
    font-family: HelveticaNeueCyr;
    font-size: 18px;
    line-height: 18px;
    text-align: center;
    text-transform: uppercase;
    color: whiteBasic;
}

@media (max-width: 768px) {

    .footer{
        padding: 15px;
    }

    .wrap{
        padding:0;
    }   

    .social{
        width: 80px;
    }

    .icon{
        width: 28px;
        height: 28px;
    }

    .contact{
        font-size: 14px;
        line-height: 17px;
    }

    .shoful{
        font-size: 14px;
        line-height: 17px;
        text-transform: initial;
    }

    .hidden{
        display: none
    }
}

